<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td></td>
<td><code v-pre="">required</code>The URL of the SuperMap iServer service.</td>
</tr>
<tr>
<td>name</td>
<td>String</td>
<td>SuperMap iServer name</td>
<td><code v-pre="">optional</code>The name of the layer.</td>
</tr>
<tr>
<td>minimumLevel</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code>The minimum tile level to request, or undefined if there is no minimum. This parameter is ignored when accessing a tiled server.</td>
</tr>
<tr>
<td>maximumLevel</td>
<td>Number</td>
<td><code v-pre="">20</code></td>
<td><code v-pre="">optional</code>The maximum tile level to request, or undefined if there is no maximum. This parameter is ignored when accessing a tiled server.</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official document of SuperMap: <strong><a href="http://support.supermap.com.cn:8090/webgl/Build/Documentation/SuperMapImageryProvider.html">SuperMapImageryProvider</a></strong></li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer}</td>
<td>Triggers when SuperMapImageryProvider is ready. It returns a core class of Cesium, a viewer instance.</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>Gets an event that is raised when the imagery provider encounters an asynchronous error.. By subscribing to the event, you will be notified of the error and can potentially recover from it. Event listeners are passed an instance of TileProviderError.</td>
</tr>
<tr>
<td>readyPromise</td>
<td>ImageryProvider</td>
<td>Gets a promise that resolves to true when the provider is ready for use.</td>
</tr>
</tbody>
</table>
</body></html></section></template>

<script>
    export default {
      data() {
        return {
          options: [
            {
              value: 'https://www.supermapol.com/realspace/services/3D-dixingyingxiang/rest/realspace/datas/MosaicResult',
              label: 'sichuan'
            },
            {
              value: 'https://www.supermapol.com/realspace/services/map--world/rest/maps/World_Google',
              label: 'google'
            }
          ],
          url: 'https://www.supermapol.com/realspace/services/3D-dixingyingxiang/rest/realspace/datas/MosaicResult',
          alpha: 1,
          brightness: 1,
          contrast: 1
        }
      },
      methods: {
        ready(cesiumInstance) {
          const { Cesium, viewer } = cesiumInstance
          this.cesiumInstance = cesiumInstance
        },
        readyPromise() {
          const { Cesium, viewer } = this.cesiumInstance
          viewer.zoomTo(this.$refs.imageryProvider.providerContainer.imageryLayer)
        }
      }
    }
  </script>